import { Component, OnInit, Input } from '@angular/core';
import { ModalController, AlertController } from '@ionic/angular';
import { HttpClient } from '@angular/common/http';
import { environment } from "../../environments/environment";
import * as moment from "moment";

@Component({
  selector: 'app-modal',
  templateUrl: './modal.page.html',
  styleUrls: ['./modal.page.scss'],
})
export class ModalPage implements OnInit {
  @Input() telefone: string;
  public observacao: string;
  public author: string;
  public information: any;
  public listaObservacao: any[];
  public listaObservacaoOriginal: any[];
  private API_URL = environment.API_URL;
  constructor( 
    private modalController: ModalController, 
    private alertController: AlertController,
    private http: HttpClient,
  ) { }

  ngOnInit() {
    this.atualizarLista();
  }

  async atualizarLista() {
    this.information = await this.http.get(`${this.API_URL}/observacoes/${this.telefone}`).toPromise();
    this.listaObservacaoOriginal = this.information.observacoes;
    this.listaObservacao = this.parseDates(this.information.observacoes); 
  }

  dismiss() {
    this.modalController.dismiss({
      'dismissed': true
    });
  }

  async save() {
    const data = {
      observacao: this.observacao,
      author: this.author,
      dataCriada: new Date()
    };
    this.listaObservacaoOriginal.push(data);    
    await this.http.put(`${this.API_URL}/observacoes/${this.telefone}`, {observacoes: this.listaObservacaoOriginal}).toPromise();
    this.observacao = '';
    this.author = '';
    this.atualizarLista();
  }

  parseDates(observacoes: any[]) {
    observacoes = observacoes.map(obs => {
      obs.dataCriada = moment(obs.dataCriada).format('DD/MM/YYYY HH:MM');
      return obs;
    });

    return observacoes;
  }

  async deleteObservacao(id) {
    const alert = await this.alertController.create({
      header: 'Atenção!',
      message: 'Tem certeza que deseja excluir essa observação?',
      buttons: [
        {
          text: 'Sim',
          handler: async () => {
            await this.http.delete(`${this.API_URL}/observacoes/${this.telefone}?id=${id}`).toPromise();
            this.atualizarLista();
          }
        }, {
          text: 'Não'
        }
      ]
    });

    await alert.present();
  }
}
