import { Injectable } from '@angular/core';
import { BehaviorSubject } from 'rxjs';

@Injectable({
  providedIn: 'root'
})
export class DataService {
  private congregacaoSource = new BehaviorSubject('default congregacao');
  currentCongregacao = this.congregacaoSource.asObservable();

  constructor() { }

  changeCongregacao(congregacao: string) {
    this.congregacaoSource.next(congregacao)
  }
}
